import {
  IonContent,
  IonHeader,
  IonItem,
  IonLabel,
  IonList,
  IonListHeader,
  IonMenu,
  IonMenuToggle,
  IonTitle,
  IonToolbar,
  IonCard,
  IonCardHeader,
  IonAvatar,
  IonRow,
  IonCol,
} from "@ionic/react";
import {
  calendar,
  chatbubbles,
  help,
  informationCircle,
  logIn,
  map,
  person,
  personAdd,
  people,
} from "ionicons/icons";
import React, { useState } from "react";
import { connect } from "react-redux";
import { RouteComponentProps, withRouter } from "react-router";
import { actions, selectors } from "../modules/auth";
import { logoutUser } from "../firebaseConfig";

const routes = {
  appPages: [
    { title: "New Group", path: "/tabs/schedule", icon: people },
    { title: "Schedule", path: "/tabs/schedule", icon: calendar },
    { title: "Chat", path: "/tabs/chat", icon: chatbubbles },
    { title: "Map", path: "/tabs/map", icon: map },
    { title: "About", path: "/tabs/about", icon: informationCircle },
  ],
  loggedInPages: [
    { title: "Account", path: "/account", icon: person },
    { title: "Support", path: "/support", icon: help },
  ],
  loggedOutPages: [
    { title: "Login", path: "/login", icon: logIn },
    { title: "Support", path: "/support", icon: help },
    { title: "Signup", path: "/signup", icon: personAdd },
  ],
};

interface Pages {
  title: string;
  path: string;
  // icon: { ios: string; md: string };
  routerDirection?: string;
}

interface StateProps {
  user: any;
}

interface DispatchProps {
  setUser: Function;
}

interface MenuProps
  extends RouteComponentProps<any>,
    StateProps,
    DispatchProps {}

const Menu: React.FC<MenuProps> = ({ user, history, setUser }) => {
  const [disableMenu, setDisableMenu] = useState(false);

  function renderlistItems(list: Pages[]) {
    return list
      .filter((route) => !!route.path)
      .map((p) => (
        <IonMenuToggle key={p.title} auto-hide="false">
          <IonItem button routerLink={p.path} routerDirection="none">
            {/* <IonIcon slot="start" icon={p.icon} /> */}
            <IonLabel>{p.title}</IonLabel>
          </IonItem>
        </IonMenuToggle>
      ));
  }
  const logout = async () => {
    await logoutUser();
    setUser(null);
    history.replace("/login");
  };
  return (
    <IonMenu disabled={disableMenu} type="overlay" contentId="main">
      <IonHeader>
        <IonToolbar>
          <IonTitle>Menu</IonTitle>
        </IonToolbar>
      </IonHeader>
      <IonContent>
        <IonCard>
          <IonCardHeader>
            <IonRow>
              <IonCol size="4">
                <IonAvatar>
                  <img src="/assets/user.png" alt="avt" />
                </IonAvatar>
              </IonCol>
              <IonCol size="8">
                <IonLabel>{user && user.displayName}</IonLabel>
                <p>{user && user.email}</p>
              </IonCol>
            </IonRow>
          </IonCardHeader>
        </IonCard>
        <IonList>
          <IonListHeader>Navigate</IonListHeader>
          {renderlistItems(routes.appPages)}
        </IonList>
        <IonList>
          <IonListHeader>Account</IonListHeader>
          {user
            ? renderlistItems(routes.loggedInPages)
            : renderlistItems(routes.loggedOutPages)}
        </IonList>
        <IonList>
          <IonListHeader>Tutorial</IonListHeader>
          <IonItem
            onClick={() => {
              setDisableMenu(true);
              history.push("/tutorial");
            }}
          >
            {/* <IonIcon slot="start" icon={hammer} /> */}
            Show Tutorial
          </IonItem>
          <IonItem
            onClick={() => {
              setDisableMenu(true);
              history.push("/tutorial");
            }}
          >
            {/* <IonIcon slot="start" icon={settings} /> */}
            Settings
          </IonItem>
        </IonList>
        <IonList>
          {/* <IonItem>
            <IonLabel>Dark Theme</IonLabel>
            <IonToggle
              checked={darkMode}
              onClick={() => setDarkMode(!darkMode)}
            />
          </IonItem> */}
        </IonList>
        <IonList>
          <IonItem onClick={logout}>Logout</IonItem>
        </IonList>
      </IonContent>
    </IonMenu>
  );
};

const mapStateToProps = (state: any) => ({
  user: selectors.getUser(state),
});

const mapDispatchToProps = (dispatch: any) => ({
  setUser: (user: any) => dispatch(actions.setUser(user)),
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Menu));
