import * as actions from './actions'

type Action = {
  type: String,
  payload: any
}
// Actions
export { actions }
// Selectors
export const selectors = {
  getUser: (state : any) => state.auth.user,
  isLoading: (state : any, actions: any) => state.ui.auth[actions].isLoading,
  isError: (state: any, actions: any) => state.ui.auth[actions].isError,
  isSuccess: (state: any, actions: any) => state.ui.auth[actions].isSuccess
}

// Default state
const defaultState = { user: null }

// Reducer
const authReducer = (state = defaultState, action: Action) => {
  const nextState = Object.assign({}, state)
  switch (action.type) {
    case actions.types.SET_USER:
      return {
        ...state,
        user: action.payload
      }

    default:
      return nextState
  }
}

export default authReducer
