import { combineReducers } from 'redux'
import { ReducerFactory } from '../ReducerFactory'
// login
import { actions } from './auth'

const defaultState = {
  isInit: true,
  isLoading: false,
  isSuccess: false,
  isError: false,
  reason: ''
}

//AUTH
const auth = combineReducers({
  login: ReducerFactory({
    defaultState,
    onStart: actions.types.LOGIN,
    onSuccess: actions.types.LOGIN_SUCCESS,
    onError: actions.types.LOGIN_FAIL,
    onDefault: actions.types.LOGOUT
  })
})

export default combineReducers({
  auth
})
