const defaultState = {
  user: {},
};
export default function reducer(
  state = defaultState,
  { type, payload }: { type: string; payload: any }
): any {
  // work with state
  switch (type) {
    case "SET_USER_STATE":
      return {
        ...state,
        user: {
          usename: payload.split("@")[0],
        },
      };
    case "SET_EMAIL_STATE":
      console.log("email", payload);
      return {
        ...state,
        user: {
          email: payload,
        },
      };
  }
  return state;
}
