import React, { useEffect, useState } from "react";
import { Provider } from "react-redux";
import { ConnectedRouter } from "connected-react-router";
import store, { history } from "./store/index";

import { Route } from "react-router-dom";
import {
  IonApp,
  IonRouterOutlet,
  IonSplitPane,
  IonSpinner,
} from "@ionic/react";
import { IonReactRouter } from "@ionic/react-router";
import Menu from "./components/Menu";

/* Core CSS required for Ionic components to work properly */
import "@ionic/react/css/core.css";

/* Basic CSS for apps built with Ionic */
import "@ionic/react/css/normalize.css";
import "@ionic/react/css/structure.css";
import "@ionic/react/css/typography.css";

/* Optional CSS utils that can be commented out */
import "@ionic/react/css/padding.css";
import "@ionic/react/css/float-elements.css";
import "@ionic/react/css/text-alignment.css";
import "@ionic/react/css/text-transformation.css";
import "@ionic/react/css/flex-utils.css";
import "@ionic/react/css/display.css";

/* Theme variables */
import "./theme/variables.css";
import MainTabs from "./pages/MainTabs";
import { connect } from "react-redux";
import Account from "./pages/Account";
import Login from "./pages/Login";
import Signup from "./pages/Signup";
import Support from "./pages/Support";
import Tutorial from "./pages/Tutorial";
import HomeOrTutorial from "./components/HomeOrTutorial";
import { getCurrentUser } from "./firebaseConfig";
import { actions, selectors } from "./modules/auth";
import PrivateRoute from "./components/PrivateRoute";

interface StateProps {
  user: any;
}

interface DispatchProps {
  setUser: Function;
}

interface RoutingSystemProps extends StateProps { }

const RoutingSystem: React.FC<RoutingSystemProps> = ({ user }: any) => {
  return (
    <IonReactRouter>
      <IonSplitPane contentId="main">
        {user ? <Menu /> : null}
        <IonRouterOutlet id="main">
          <PrivateRoute path="/tabs" component={MainTabs} />
          <PrivateRoute path="/account" component={Account} />
          <PrivateRoute path="/support" component={Support} />
          <PrivateRoute path="/tutorial" component={Tutorial} />
          <PrivateRoute path="/" component={HomeOrTutorial} exact />
          <Route path="/login" component={Login} exact />
          <Route path="/signup" component={Signup} exact />
        </IonRouterOutlet>
      </IonSplitPane>
    </IonReactRouter>
  );
};

const App: React.FC = () => {
  return (
    <Provider store={store}>
      <ConnectedRouter history={history}>
        <IonicAppConnected />
      </ConnectedRouter>
    </Provider>
  );
};

interface IonicAppProps extends StateProps, DispatchProps { }

const IonicApp: React.FC<IonicAppProps> = ({ setUser, user }) => {
  const [busy, setBusy] = useState(true);
  useEffect(() => {
    async function getUser() {
      const user: any = await getCurrentUser();
      if (user) {
        setUser(user);
      } else {
        window.history.replaceState({}, "", "/login");
      }
      setBusy(false);
    }
    getUser();
    // eslint-disable-next-line
  }, []);
  return (
    // <IonApp className={`${darkMode ? "dark-theme" : ""}`}>
    <IonApp>{busy ? <IonSpinner /> : <RoutingSystem user={user} />}</IonApp>
  );
};

export default App;

const mapStateToProps = (state: any) => ({
  user: selectors.getUser(state),
});
const IonicAppConnected = connect(mapStateToProps, (dispatch: any) => ({
  setUser: (user: any) => dispatch(actions.setUser(user)),
}))(IonicApp);
