import React from "react";
import {
  IonHeader,
  IonToolbar,
  IonTitle,
  IonAvatar,
  IonItem,
  IonLabel,
  IonContent,
  IonSearchbar,
  IonPage,
  IonButtons,
  IonMenuButton,
  IonList,
  IonIcon,
} from "@ionic/react";
import ShareSocialFab from "../components/ShareSocialFab";
import { connect } from "../data/connect";
import * as selectors from "../data/selectors";
import { add } from "ionicons/icons";
// import { person-add-outline } from 'ionicons/icons';
interface OwnProps {}

interface StateProps {
  // classes: Class[];
}

interface DispatchProps {}

interface ContactProps extends OwnProps, StateProps, DispatchProps {}

const Contact: React.FC<ContactProps> = () => {
  return (
    <IonPage id="class-list">
      <IonHeader>
        <IonToolbar>
          <IonButtons slot="start">
            <IonMenuButton />
          </IonButtons>
          <IonTitle>Contact</IonTitle>
        </IonToolbar>
      </IonHeader>
      <IonHeader> </IonHeader>

      <IonContent className={`outer-content`}>
        <IonList>
          <IonSearchbar></IonSearchbar>
          <IonToolbar>
            <IonItem>
              <IonIcon icon={add}></IonIcon>
              <IonLabel>Invite Friend</IonLabel>
            </IonItem>
          </IonToolbar>
          <IonItem lines="none">
            <IonAvatar slot="start">
              <img
                src="https://photo-1-baomoi.zadn.vn/w1000_r1/2019_07_11_188_31416921/2653da23ac63453d1c72.jpg"
                alt="avt"
              />
            </IonAvatar>
            <IonLabel>Duc</IonLabel>
          </IonItem>
        </IonList>
      </IonContent>
      <ShareSocialFab />
    </IonPage>
  );
};

export default connect<OwnProps, StateProps, DispatchProps>({
  mapStateToProps: (state) => ({
    classes: selectors.getClasses(state),
    speakerSessions: selectors.getSpeakerSessions(state),
  }),
  component: React.memo(Contact),
});
