import React from "react";
import {
  IonTabs,
  IonRouterOutlet,
  IonTabBar,
  IonTabButton,
  IonIcon,
  IonLabel,
} from "@ionic/react";
import { Route, Redirect } from "react-router";
import {
  chatbubbles,
  map,
  informationCircle,
  peopleCircleOutline,
} from "ionicons/icons";
import SchedulePage from "./SchedulePage";
import SpeakerList from "./SpeakerList";
import ClassList from "./Messenger";
import Chat from "./Chat";

import SpeakerDetail from "./SpeakerDetail";
import SessionDetail from "./SessionDetail";
import MapView from "./MapView";
import About from "./About";
import Contact from "./Contact";
import { Settings } from "./Settings";
import Account from './Account'
interface MainTabsProps { }

const MainTabs: React.FC<MainTabsProps> = () => {
  return (
    <IonTabs>
      <IonRouterOutlet>
        <Redirect exact path="/tabs" to="/tabs/chat" />
        {/* 
          Using the render method prop cuts down the number of renders your components will have due to route changes.
          Use the component prop when your component depends on the RouterComponentProps passed in automatically.        
        */}
        <Route
          path="/tabs/schedule"
          render={() => <SchedulePage />}
          exact={true}
        />
        <Route
          path="/tabs/speakers"
          render={() => <SpeakerList />}
          exact={true}
        />
        <Route path="/tabs/chat" render={() => <ClassList />} exact={true} />
        <Route path="/tabs/chat/detail" render={() => <Chat />} exact={true} />

        <Route
          path="/tabs/speakers/:id"
          component={SpeakerDetail}
          exact={true}
        />
        <Route path="/tabs/contact" component={Contact} exact={true} />
        <Route path="/tabs/schedule/:id" component={SessionDetail} />
        <Route path="/tabs/speakers/sessions/:id" component={SessionDetail} />
        <Route path="/tabs/map" render={() => <MapView />} exact={true} />
        {/* <Route path="/tabs/chat" render={() => <Chat />} exact={true} /> */}
        <Route path='/account' component={Account} exact />
        {/* <Route path="/tabs/login1" render={() => <Login1 />} exact={true} /> */}

        <Route path="/tabs/setting" component={Settings} />
      </IonRouterOutlet>
      <IonTabBar slot="bottom">
        <IonTabButton tab="chat" href="/tabs/chat">
          <IonIcon icon={chatbubbles} />
          <IonLabel>Chat</IonLabel>
        </IonTabButton>
        <IonTabButton tab="contact" href="/tabs/contact">
          <IonIcon icon={peopleCircleOutline} />
          <IonLabel>Contact</IonLabel>
        </IonTabButton>
        <IonTabButton tab="map" href="/tabs/map">
          <IonIcon icon={map} />
          <IonLabel>Map</IonLabel>
        </IonTabButton>
        <IonTabButton tab="about" href="/account">
          <IonIcon icon={informationCircle} />
          <IonLabel>Settings</IonLabel>
        </IonTabButton>
      </IonTabBar>
    </IonTabs>
  );
};

export default MainTabs;
