import React from "react";
import {
  IonHeader,
  IonToolbar,
  IonTitle,
  IonContent,
  IonPage,
  IonButtons,
  IonMenuButton,
} from "@ionic/react";
import { User } from "../models/User";
import { connect } from "../data/connect";
import * as selectors from "../data/selectors";
import "./Class.scss";
import ChatList from "../components/ChatList";

interface OwnProps {}

interface StateProps {
  classes: User[];
}

interface DispatchProps {}

interface ClassListProps extends OwnProps, StateProps, DispatchProps {}

const ClassList: React.FC<ClassListProps> = ({ classes }) => {
  return (
    <IonPage id="class-list">
      <IonHeader>
        <IonToolbar>
          <IonButtons slot="start">
            <IonMenuButton />
          </IonButtons>
          <IonTitle>Message</IonTitle>
        </IonToolbar>
      </IonHeader>
      <IonContent className={`outer-content`}>
        <ChatList />
      </IonContent>
    </IonPage>
  );
};

export default connect<OwnProps, StateProps, DispatchProps>({
  mapStateToProps: (state) => ({
    classes: selectors.getClasses(state),
    speakerSessions: selectors.getSpeakerSessions(state),
  }),
  component: React.memo(ClassList),
});
