import React from "react";
import { IonItem, IonAvatar, IonLabel, IonBadge, IonNote } from "@ionic/react";
const ChatListItem = () => {
  return (
    <IonItem routerLink="/tabs/chat/detail">
      <IonAvatar slot="start">
        <img src="/assets/user.png" alt="avt" />
      </IonAvatar>
      <IonLabel>
        <h2>Finn</h2>
        <p>Listen, I've had a pretty messed up day...</p>
      </IonLabel>
      <IonNote slot="end">
        <p style={{ fontSize: "12px" }}>8:00 AM</p>
        <IonBadge color="light">22</IonBadge>
      </IonNote>
    </IonItem>
  );
};

export default ChatListItem;
