import {
  IonList,
  IonListHeader,
  IonItemSliding,
  IonItemOptions,
  IonItemOption,
  IonActionSheet,
} from "@ionic/react";
import React, { useState } from "react";
import MessageListItem from "./ChatListItem";

import { SessionGroup } from "../models/SessionGroup";
import { connect } from "../data/connect";
import { addFavorite, removeFavorite } from "../data/sessions/sessions.actions";

interface OwnProps {
  sessionGroups: SessionGroup[];
  listType: "all" | "favorites";
  hide: boolean;
}

interface StateProps {
  favoriteSessions: number[];
}

interface DispatchProps {
  addFavorite: typeof addFavorite;
  removeFavorite: typeof removeFavorite;
}

interface ChatListProps extends OwnProps, StateProps, DispatchProps {}

const ChatList: React.FC<ChatListProps> = ({ hide }) => {
  const [showActionSheet, setShowActionSheet] = useState(false);

  return (
    <>
      <IonList style={hide ? { display: "none" } : {}}>
        <IonListHeader>Recent Conversations</IonListHeader>
        <IonItemSliding>
          <MessageListItem />
          <IonItemOptions side="end">
            <IonItemOption onClick={() => setShowActionSheet(true)}>
              Delete
            </IonItemOption>
          </IonItemOptions>
        </IonItemSliding>
      </IonList>
      <IonActionSheet
        isOpen={showActionSheet}
        onDidDismiss={() => setShowActionSheet(false)}
        buttons={[
          {
            text: "Delete",
            role: "destructive",
            handler: () => {
              console.log("Delete clicked");
            },
          },
          {
            text: "Share",
            handler: () => {
              console.log("Share clicked");
            },
          },
          {
            text: "Favorite",
            handler: () => {
              console.log("Favorite clicked");
            },
          },
          {
            text: "Cancel",
            role: "cancel",
            handler: () => {
              console.log("Cancel clicked");
            },
          },
        ]}
      ></IonActionSheet>
    </>
  );
};

export default connect({
  mapStateToProps: (state) => ({
    favoriteSessions: state.data.favorites,
  }),
  mapDispatchToProps: {
    addFavorite,
    removeFavorite,
  },
  component: ChatList,
});
